<style lang="less">
@baseColor: #ffc124;
@borderColor: #c8c8c8;
@activeColor: #fff;
@page_margin: 30px;
@color: #FFC124;
.loading,
.reject{
	text-align: center;
	margin-top: 250px;
}
.main_wrap{
	position: relative;
	.official_about{
		right: 32px;
		bottom: 35px;
	}
}
.time_box{
	width: 900px;
	margin: 0 auto;
	.time_goods{
		.goods_item{
			margin-top: 40px;
			.cover{
				width: 180px;
				height: 180px;
				padding: 6px;
				border: 1px solid @borderColor;
				border-radius: 3px;
				margin-right: 6px;

				img{
					width: 100%;
				}
			}

			.goods_bd{
				border: 1px solid @borderColor;
				border-radius: 3px;

				&--info{
					h4{
						line-height: 44px;
						font-weight: normal;
						font-size: 18px;
						color: #333;
						text-align: center;
						border-bottom: 1px solid @borderColor;
					}
					border-right: 1px solid @borderColor;

					.content{
						color: rgba(0,0,0,0.54);
						padding: 10px;
						line-height: 1.5;
					}
					.introduction{
						line-height: 44px;
					    font-weight: normal;
					    font-size: 20px;
					    text-align: center;
					    border-top: 1px solid #c8c8c8;
					    margin-top: 62px;
					    display: flex;
					    flex-direction: row;
					    padding: 0 10px;
					    div{
					    	flex: 1;
					    	span{
					    		font-size: 14px;
					    		color: #676767;
					    	}
					    }
					    .icon{
					    	display: inline-block;
					    	height: 21px;
							width: 21px;
					    }
					    .seller{
					    	display: flex;
					    	flex-direction: row;
					    	align-items: center;
					    	.portrait_wrap{
						    	width: 25px;
						    	height: 25px;
						    	background-color: #ffc124;
						    	display: flex;
						    	justify-content: center;
						    	align-items: center;
						    	border-radius: 3px;
						    	margin-right: 5px;
						    	.portrait{
							    	background: url('../../../../assets/img/searchsprite.png') -0px -0px no-repeat;
							    }
						    }
					    }
					    .time{
					    	.time{
					    		height: 17px;
								width: 15px;
						    	background: url('../../../../assets/img/searchsprite.png') -21px -0px no-repeat;
					    	}
					    }
					    .watch{
					    	.watch{
					    		height: 12px;
								width: 21px;
								background: url('../../../../assets/img/searchsprite.png') -58px -0px no-repeat;
					    	}
					    }
					    .address{
					    	.address{
					    		height: 16px;
								width: 11px;
								background: url('../../../../assets/img/searchsprite.png') -36px -0px no-repeat;
					    	}
					    }
						.price{
							.price{
								height: 14px;
								width: 11px;
								background: url(http://gopng/static/images/sprite.png) -47px -0px no-repeat;
							}
						}
					}
				}
			}
		}
	}

}
.pages_box{
    text-align: center;
    margin: 2*@page_margin 0 @page_margin 0;
    li{
        display: inline-block;
        background-color: #FFF;
        margin: 0 15px;
        a{
            display: inline-block;
            padding: 10px;
            &.active{
                color: @activeColor;
                background: @color;
                border: solid 1px @color;
            }
            &.unactive{
                color: @color;
                border: solid 1px @color;
            }
            &.unable{
                color: #fff;
                background: #ddd;
                border-width: solid 1px #ddd;
            }
        }
    }
    .hidden{
    	display: none;
    }
}
</style>

<template>
	<div class="main_wrap">
	<div class="time_box" v-if="searchGoodsState === 'resolve'">
		<div class="time_goods" v-for="its in resultGoods">
			<div class="goods_item flex-wrap">
				<div class="cover">
					<img :src="its.informationimage" alt="" width="100%" height="100%">
				</div>
				<div class="goods_bd flex-con flex-wrap">
					<div class="goods_bd--info flex-con">
						<h4>按价格</h4>
						<div class="content">
							<p>{{its.informationdes}}</p>
						</div>
						<div class="introduction">
							<div class="seller">
								<span class="portrait_wrap">
									<span class="icon portrait"></span>
								</span>
								<span>{{its.username}}</span>
							</div>
							<div class="time">
								<span class="icon time"></span>
								<span>{{its.time}}</span>
							</div>
							<div class="watch">
								<span class="icon watch"></span>
								<span>{{its.watched}}</span>
							</div>
							<div class="address">
								<span class="icon address"></span>
								<span>{{its.address}}</span>
							</div>
							<div class="price">
								<span class="icon price"></span>
								<span>￥{{its.exprice}}</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div v-show="searchGoodsState=='pending'" class="loading">
            正在加载数据中...
    </div>
    <div v-show="searchGoodsState=='reject'" class="reject">
            数据加载失败！检查您的网络...
    </div>
	<ul class="pages_box" >
            <li v-for="index in pages" @click="gotoPage(index)" class="item">
              <a href="javascript:void(0)" :class="[current == index ? 'active' : 'unactive']">{{index}}</a>
            </li>
            <li v-show="current != 1" @click="current-- && gotoPage(current)" class="item">
                <a href="javascript:void(0)" class="unactive">上一页</a>
            </li>
            <li v-show="allpage != current && allpage != 0 " @click="current++ && gotoPage(current++)" :class="[current==curAllPages ? 'hidden' : 'item']">
                <a href="javascript:void(0)" class="unactive">下一页</a>
            </li>
        </ul>
    <ui-Official></ui-Official>
    </div>
</template>

<script>
import uiOfficial from '../../../../components/official'
export default {
	data() {
		return {
			searchGoodsState: 'pending',
			resultGoods: [],
			curAllPages: 0,
			pages: [],
			current:1,
            showItem:5
		}
	},
	 computed:{
          pages:function(){
                var pag = [];
                  if( this.current < this.showItem ){ //如果当前的激活的项 小于要显示的条数
                       //总页数和要显示的条数那个大就显示多少条
                       var i = Math.min(this.showItem,this.curAllPages);
                       while(i){
                           pag.unshift(i--);
                       }
                   }else{ //当前页数大于显示页数了
                       var middle = this.current - Math.floor(this.showItem / 2 ),//从哪里开始
                           i = this.showItem;
                       if( middle >  (this.curAllPages - this.showItem)  ){
                           middle = (this.curAllPages - this.showItem) + 1
                       }
                       while(i--){
                           pag.push( middle++ );
                       }
                   }
                 return pag
               }
      },
    ready() {
		this.searchGoodsCounts(this.$route.params.key);
		this.searchGoodsInfo(this.$route.params.key, 1);
    },
	methods: {
		searchGoodsCounts(key) {
            setTimeout(() => {
                let goodsCounts = require('../../../../../mock/searchgoodsCounts'); //请求
                goodsCounts = goodsCounts.default;
                this.curAllPages = Math.floor(goodsCounts/4) + ( goodsCounts%4 ? 1 : 0 );
            }, 1000);
        },
        //按页面请求对应分类商品和请求对应类型商品应为同一接口
        searchGoodsInfo(key, page) {
            setTimeout(() => {
                let goodslist = require('../../../../../mock/searchgoodslistprice'); //假装请求最新商品
                goodslist = goodslist.default;
                // this.resultGoods = goodslist.data.recordList;
                // this.searchGoodsState = 'resolve';
                fetch("http://xianyijun.cn:8080/information/search", {
					method: "POST",
					headers: {
						"Content-Type": "application/json"
					},
					body: JSON.stringify({
						"keyword": key
					}),
				}).then((res) => {
					return res.json()
				}).then(data => {
					this.resultGoods = data.data.recordList;
					this.curAllPages = data.data.recordCount;
					this.searchGoodsState = 'resolve';
				});
            }, 1000);
        },
		//按页请求
		gotoPage(index){
			this.searchGoodsState = 'pending';
			this.current = index;
			this.searchGoodsInfo(this.$route.params.key, index);
		}
	},
	components: {
		uiOfficial
	}
}
</script>


